
.asidewarp {
  position: absolute;
  left: 10px;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 380px;
  height: 90%;
  overflow: hidden;
  border-radius: 6px;
  background: rgba(37, 80, 128, 0.9);
}
.asidewarp .inner-warp {
    position: relative;
    width: 100%;
    height: 100%;
}
.asidewarp .inner-warp .title {
      width: 100%;
      height: 70px;
      text-align: left;
      line-height: 70px;
      padding-left: 17px;
      padding-right: 17px;
      font-size: 20px;
      color: #ffffff;
      font-weight: bold;
      border-radius: 6px;
      background: #255080;
}
.asidewarp .inner-warp .title a {
        float: right;
        color: #ffffff;
        font-size: 15px;
}
.asidewarp .number-title {
    width: 100%;
    height: auto;
    padding-left: 17px;
    padding-right: 17px;
    padding-top: 10px;
    padding-bottom: 10px;
}
.asidewarp .number-title .list {
      width: 100%;
      height: 45px;
      margin-bottom: 5px;
      padding-left: 35px;
      padding-right: 35px;
      line-height: 45px;
      color: #ffffff;
      font-size: 20px;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 6px;
      cursor: pointer;
}
.asidewarp .number-title .list i {
        float: right;
        font-style: normal;
}
.asidewarp .number-title .list:last-child {
      margin-bottom: 0;
}
.asidewarp .place-number-warp {
    position: relative;
    width: 100%;
    height: calc(100% - 260px);
    margin: 0;
    padding-left: 17px;
    padding-right: 17px;
    list-style: none;
    overflow: hidden;
}
.asidewarp .place-number-warp .list {
      position: relative;
      width: 100%;
      margin-bottom: 5px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 6px;
      transition: -webkit-transform .3s ease;
      transition: transform .3s ease;
      transition: transform .3s ease, -webkit-transform .3s ease;
}
.asidewarp .place-number-warp .list a {
        height: 45px;
        display: block;
        position: relative;
        padding-left: 35px;
        padding-right: 35px;
        line-height: 45px;
        color: #ffffff;
        font-size: 20px;
        box-sizing: border-box;
}
.asidewarp .place-number-warp .list:hover {
      background: rgba(255, 255, 255, 0.5);
}
.asidewarp .place-number-warp span {
      display: inline-block;
      max-width: 50%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.asidewarp .place-number-warp .number {
      float: right;
}
.asidewarp .place-number-warp .inner-list {
      position: relative;
      width: 100%;
      max-height: 0;
      margin: 0;
      padding: 0;
      list-style: none;
      border-bottom-left-radius: 6px;
      border-bottom-right-radius: 6px;
      overflow: hidden;
      background: rgba(255, 255, 255, 0.2);
      transition: max-height .3s;
}
.asidewarp .place-number-warp .inner-list.active {
        max-height: 200px;
}
.asidewarp .place-number-warp .inner-list i {
        font-style: normal;
        float: right;
}
.asidewarp .place-number-warp .inner-list li {
      position: relative;
      width: 100%;
      font-size: 20px;
      height: auto;
      line-height: 100%;
      color: #ffffff;
      cursor: pointer;
}
.asidewarp .place-number-warp .inner-list li:hover {
        background: #255080;
}
.warp-box {
  width: 100%;
  height: auto;
}



