
.people-warp {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 900px;
  height: 700px;
  text-align: center;
  border-radius: 6px;
  border: 2px solid rgba(83, 137, 216, 0.9);
  background: rgba(37, 80, 128, 0.9);
}
.people-warp .head-title {
    position: relative;
    width: 100%;
    height: 160px;
    padding-left: 200px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 35px;
}
.people-warp .head-title .pic {
      position: absolute;
      top: 15px;
      left: 35px;
      width: 150px;
      height: 130px;
      border: 1px solid #ffffff;
      background-size: cover;
}
.people-warp .head-title .content-wrap {
      widows: 100%;
      height: calc(100%/3);
      text-align: left;
      font-size: 20px;
      line-height: 43px;
}
.people-warp .head-title span.name {
      font-size: 25px;
}
.people-warp .head-title span {
      color: #ffffff;
}
.people-warp .git-in-icon {
    position: relative;
    max-width: 30%;
    height: 30px;
    margin-top: 10px;
    padding-right: 25px;
    padding-left: 5px;
    line-height: 30px;
    border-radius: 4px;
    border: 1px solid #28ce34;
    background: rgba(40, 206, 52, 0.3);
    text-align: center;
    color: #ffffff;
    display: inline-block;
    float: right;
    display: block;
}
.people-warp .git-in-icon .iconfont {
      color: #ffffff;
}
.people-warp .cont-warp {
    width: 100%;
    height: calc(100% - 160px);
    padding: 0 35px 75px 35px;
}
.people-warp .cont-warp:before, .people-warp .cont-warp:after {
      content: " ";
      display: table;
}
.people-warp .cont-warp:after {
      clear: both;
}
.people-warp .message-warp {
    width: calc(40% - 10px);
    height: 100%;
    padding: 15px;
    border-radius: 6px;
    border: 1px solid #348ac7;
    float: left;
    color: #ffffff;
}
.people-warp .server-warp {
    width: calc(60% - 10px);
    height: 100%;
    padding: 15px;
    border-radius: 6px;
    border: 1px solid #348ac7;
    float: right;
    color: #ffffff;
}
.people-warp .go-out-message {
    width: 100%;
    height: 150px;
    background: rgba(0, 0, 0, 0.3);
}
.people-warp .go-out-message .title {
      width: 100%;
      height: 40px;
      margin-bottom: 10px;
      padding-right: 10px;
      padding-left: 10px;
      line-height: 40px;
      text-align: left;
      font-size: 18px;
      color: #ffffff;
      background: #28ce34;
}
.people-warp .go-out-message p {
      margin: 0 0 10px 0;
      padding-right: 10px;
      padding-left: 10px;
      text-align: left;
      font-size: 16px;
}
.people-warp .go-out-map {
    width: 100%;
    height: calc(100% - 160px);
    margin-top: 10px;
    background: rgba(0, 0, 0, 0.3);
}
.people-warp .go-out-map .title {
      width: 100%;
      height: 40px;
      padding-right: 10px;
      padding-left: 10px;
      line-height: 40px;
      text-align: left;
      font-size: 18px;
      color: #ffffff;
      background: #28ce34;
}
.people-warp .title-go-out {
    width: 100%;
    height: 40px;
    padding-right: 10px;
    padding-left: 10px;
    line-height: 40px;
    text-align: left;
    font-size: 18px;
    color: #ffffff;
    background: #28ce34;
}



