
.personnel-inner {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  list-style: none;
  cursor: pointer;
}
.personnel-inner .warp-pre {
    width: 100%;
    height: auto;
}
.personnel-inner h3 {
    color: #ffffff;
}
.personnel-inner li {
    position: relative;
    width: 100%;
    height: auto;
}
.personnel-inner li a {
      position: relative;
      width: 100%;
      height: auto;
      padding-top: 10px;
      padding-left: 100px;
      color: #ffffff;
      display: block;
}
.personnel-inner li .pic {
      position: absolute;
      left: 10px;
      top: 10px;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-size: cover;
}
.personnel-inner li span {
      display: inline-block;
      padding-bottom: 10px;
      font-size: 18px;
}



