
.office-detail-list {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.office-list-warp {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  list-style: noen;
}
.office-list-warp .list {
    width: 100%;
    height: auto;
}
.office-list-warp .list a {
      display: block;
      position: relative;
      width: 100%;
      height: 100px;
}
.office-list-warp .list.active .time-name {
      background: #28ce34;
}
.office-list-warp .list.active .icon-finish {
      display: none;
}
.office-list-warp .list.active .git-in-icon {
      display: block;
}
.office-list-warp .time-name, .office-list-warp .git-in {
    width: 100%;
    height: 50%;
    padding-left: 15px;
    padding-right: 15px;
    text-align: left;
    line-height: 50px;
    color: #ffffff;
    font-size: 16px;
}
.office-list-warp .time-name {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    background: rgba(51, 51, 51, 0.8);
}
.office-list-warp .time-name span {
      max-width: 20%;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.office-list-warp .time-name .inner-thing, .office-list-warp .time-name .inner-name {
      max-width: 40%;
}
.office-list-warp .git-in {
    position: relative;
    background: rgba(51, 51, 51, 0.3);
}
.office-list-warp .git-in:before, .office-list-warp .git-in:after {
      content: " ";
      display: table;
}
.office-list-warp .git-in:after {
      clear: both;
}
.office-list-warp .git-in .git-in-text {
      max-width: 70%;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      float: left;
}
.office-list-warp .git-in .git-in-icon {
      position: relative;
      max-width: 30%;
      height: 30px;
      margin-top: 10px;
      padding-right: 25px;
      padding-left: 5px;
      line-height: 30px;
      border-radius: 4px;
      border: 1px solid #28ce34;
      background: rgba(40, 206, 52, 0.3);
      text-align: center;
      color: #ffffff;
      display: inline-block;
      float: right;
      display: none;
}
.office-list-warp .git-in .git-in-icon .iconfont {
        color: #ffffff;
}
.office-list-warp .git-in i {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 5px;
      margin: auto;
      font-size: 15px;
      color: #28ce34;
}



