
.amap-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.amap-container .title {
    position: absolute;
    bottom: 0;
    right: 0;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    height: 80px;
    background: #255080;
    color: #ffffff;
    font-size: 30px;
}
.amap-container .title .sj {
      position: absolute;
      bottom: 0;
      left: -60px;
      width: 0;
      height: 0;
      border-bottom: 80px solid #255080;
      border-left: 60px solid transparent;
}
.marker {
  width: 19px;
  height: 33px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  cursor: pointer;
}
.marker.City {
    width: 59px;
    height: 73px;
    background-image: url(../../static/images/City.png);
}
.marker.County {
    width: 39px;
    height: 53px;
    background-image: url(../../static/images/County.png);
}
.marker.Street {
    width: 39px;
    height: 53px;
    background-image: url(../../static/images/Street.png);
}
.marker.Community {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #28ce34;
}
.stract {
  background-color: #348ac7;
}
.stract:hover {
    background-color: #0fa3b1;
    opacity: 1;
}
.dot {
  width: 20px;
  height: 31px;
  background-image: url(../assets/images/mark_bs.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  cursor: pointer;
}
.amap-info-content {
  padding: 0;
  border: 0;
}
.amap-info-content a {
    display: block;
    width: 100%;
    height: 50px;
    text-align: center;
    border-top: 1px solid #eeeeee;
    line-height: 50px;
    color: #808080;
    font-size: 16px;
}
.amap-info-close {
  margin-top: 15px;
  margin-right: 15px;
  font-size: 24px;
  color: #ffffff;
  cursor: pointer;
}
.amap-info-close:hover {
    color: #ffffff;
}
.amap-info-outer, .amap-menu-outer {
  border-radius: 5px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}
.amap-info-outer:hover, .amap-menu-outer:hover {
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}
.amap-info-sharp {
  position: absolute;
  left: -15px;
  bottom: 0;
  top: 0;
  margin: auto;
  background: none;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-right: 15px solid #ffffff;
  border-bottom: 15px solid transparent;
}
.info-bg {
  position: relative;
  width: 450px;
  height: 60px;
  padding-left: 25px;
  padding-right: 25px;
  text-align: left;
  line-height: 60px;
  font-size: 22px;
  color: #ffffff;
}
.info-bg.City {
    background: #f53837;
}
.info-bg.County {
    background: #f4d038;
}
.info-bg.Street {
    background: #ef671b;
}
.info-bg.Community {
    background: #70bb4d;
}
.info-bg i {
    margin-right: 10px;
    font-size: 22px;
}
.info-body {
  width: 100%;
  height: 190px;
  background-color: #ffffff;
}
.info-body .img-warp {
    position: relative;
    height: 100%;
    width: 150px;
}
.info-body .img-warp .img-bg {
      position: absolute;
      left: 0;
      right: 0;
      top: 15px;
      margin: auto;
      width: 130px;
      height: 130px;
}
.info-body ul {
    float: right;
    width: 314px;
    height: 100%;
    margin: 0;
    padding: 10px 15px 10px 15px;
    list-style: none;
}
.info-body ul li {
      width: 100%;
      height: 25%;
}
.info-body ul li span {
        width: 25%;
        height: 100%;
        box-sizing: border-box;
        float: left;
        color: #808080;
        font-size: 16px;
        display: block;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
}
.info-body ul li .inner {
        padding-left: 5px;
        width: 75%;
        float: right;
        color: #333333;
}
.router-warp {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(50px, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-50px, 0);
  transform: translate(-100%, 0);
}



