
.office-inner {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  list-style: none;
  cursor: pointer;
}
.office-inner .warp-pre {
    width: 100%;
    height: auto;
}
.office-inner .warp-pre:before, .office-inner .warp-pre:after {
      content: " ";
      display: table;
}
.office-inner .warp-pre:after {
      clear: both;
}
.office-inner h3 {
    color: #ffffff;
}
.office-inner li {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
    color: #ffffff;
}
.office-inner li:before, .office-inner li:after {
      content: " ";
      display: table;
}
.office-inner li:after {
      clear: both;
}
.office-inner li a {
      position: relative;
      width: 100%;
      height: 85px;
      display: block;
      border-radius: 6px;
      overflow: hidden;
      background: rgba(52, 138, 199, 0.5);
}
.office-inner li a:before, .office-inner li a:after {
        content: " ";
        display: table;
}
.office-inner li a:after {
        clear: both;
}
.office-inner li .head-warp {
      width: 200px;
      height: 100%;
      float: left;
      text-align: center;
      line-height: 85px;
      color: #ffffff;
      font-size: 20px;
      border-radius: 6px;
      background: rgba(51, 51, 51, 0.5);
}
.office-inner li .head-warp.active {
      background: #28ce34;
}
.office-inner li .body-warp {
      width: calc(100% - 200px);
      height: 100%;
      padding-left: 20px;
      padding-top: 15px;
      font-size: 20px;
      float: left;
}
.office-inner li .body-warp span {
        display: inline-block;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #ffffff;
}
.office-inner li .body-warp i {
        font-style: normal;
        color: #28ce34;
}



