
.canmar {
  position: relative;
  width: 100%;
  height: 100%;
}
.canmar:after {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    width: 0;
    height: 0;
    border-top: 25px solid transparent;
    border-left: 40px solid #ffffff;
    border-bottom: 25px solid transparent;
    content: "";
    cursor: pointer;
}



