
.office-detail-warp {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 900px;
  height: 700px;
  text-align: center;
  border-radius: 6px;
  border: 2px solid rgba(83, 137, 216, 0.9);
  background: rgba(37, 80, 128, 0.9);
}
.office-detail-warp .title {
    position: relative;
    padding: 30px 70px;
    display: inline-block;
    line-height: 100%;
    font-size: 20px;
    color: #ffffff;
    background: rgba(255, 255, 255, 0.3);
}
.office-detail-warp .title:after {
      position: absolute;
      right: -80px;
      top: 0;
      width: 0;
      height: 0;
      border-top: 80px solid rgba(255, 255, 255, 0.3);
      border-right: 80px solid transparent;
      content: "";
}
.office-detail-warp .title:before {
      position: absolute;
      left: -80px;
      top: 0;
      width: 0;
      height: 0;
      border-top: 80px solid rgba(255, 255, 255, 0.3);
      border-left: 80px solid transparent;
      content: "";
}
.office-detail-warp .cont-warp {
    width: 100%;
    height: calc(100% - 80px);
    padding: 50px 35px 75px 35px;
}
.office-detail-warp .cont-warp:before, .office-detail-warp .cont-warp:after {
      content: " ";
      display: table;
}
.office-detail-warp .cont-warp:after {
      clear: both;
}
.office-detail-warp .message-warp {
    width: calc(40% - 10px);
    height: 100%;
    padding: 15px;
    border-radius: 6px;
    border: 1px solid #348ac7;
    float: left;
    color: #ffffff;
}
.office-detail-warp .message-warp h3 {
      margin: 0;
      line-height: 100%;
      text-align: left;
      font-weight: normal;
}
.office-detail-warp .server-warp {
    width: calc(60% - 10px);
    height: 100%;
    padding: 15px;
    border-radius: 6px;
    border: 1px solid #348ac7;
    float: right;
    color: #ffffff;
}
.office-detail-warp .server-warp h3 {
      margin: 0;
      line-height: 100%;
      text-align: left;
      font-weight: normal;
}
.office-detail-warp .server-warp .server-list-warp {
      width: 100%;
      height: calc(100% - 39px);
}
.office-detail-warp .title-messag {
    width: 100%;
    height: 150px;
    margin-top: 15px;
    margin-bottom: 15px;
}
.office-detail-warp .title-messag .category {
      width: 100%;
      height: auto;
      padding: 10px 15px;
      line-height: 100%;
      text-align: left;
      font-size: 18px;
      background: rgba(51, 51, 51, 0.8);
}
.office-detail-warp .title-messag .pic-warp {
      position: relative;
      width: 100%;
      height: calc(100% - 38px);
      padding-left: 100px;
      padding-right: 15px;
      background: rgba(51, 51, 51, 0.2);
}
.office-detail-warp .title-messag .pic-warp span {
        width: 100%;
        height: 50%;
        line-height: 56px;
        display: inline-block;
        font-size: 18px;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.office-detail-warp .title-messag .pic-warp .name {
        color: #28ce34;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.office-detail-warp .title-messag .pic-w-inner {
      position: absolute;
      left: 10px;
      top: 0;
      bottom: 0;
      margin: auto;
      width: 75px;
      height: 75px;
      border-radius: 50%;
      border: 2px solid #ffffff;
      background-size: cover;
}
.office-detail-warp .today-all-number {
    width: calc(50% - 5px);
    height: 100px;
    float: left;
    font-size: 18px;
    font-weight: bold;
    background: rgba(51, 51, 51, 0.2);
}
.office-detail-warp .today-all-number h4 {
      margin: 0 0 15px 0;
      padding-top: 10px;
      padding-bottom: 10px;
      background: rgba(51, 51, 51, 0.8);
}
.office-detail-warp .today-finish-number {
    width: calc(50% - 5px);
    height: 100px;
    float: right;
    font-size: 18px;
    font-weight: bold;
    background: rgba(51, 51, 51, 0.2);
}
.office-detail-warp .today-finish-number h4 {
      margin: 0 0 15px 0;
      padding-top: 10px;
      padding-bottom: 10px;
      background: rgba(51, 51, 51, 0.8);
}
.office-detail-warp .canmar-warp {
    position: relative;
    width: 100%;
    height: 140px;
    margin-top: 15px;
    float: left;
    border: 3px solid #ffffff;
}
.office-detail-warp .canmar-warp.active {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
      background: #333333;
      z-index: 999;
      transition: all .3s ease;
}



