.pull-left {
  float: left !important;
}
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.fade {
  opacity: 0;
  transition: opacity .15s linear;
}
.fade.in {
  opacity: 1;
}
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  font-size: 10px;
  -webkit-tap-highlight-color: transparent;
}
body {
  position: relative;
  width: 100%;
  height: 100%;
  line-height: 1.42857;
  font-family: "Tahoma", "Helvetica Neue", "Lantinghei SC", Arial, "Microsoft Yahei", "Hiragino Sans GB", STXihei, Helvetica, sans-serif;
  font-size: 14px;
  color: #585858;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
a {
  color: #255080;
  text-decoration: none;
}
a:hover, a:focus {
    color: #255080;
    outline: none;
}
figure {
  margin: 0;
}
img {
  vertical-align: middle;
}
h1, .h1 {
  font-size: 36px;
}
h2, .h2 {
  font-size: 30px;
}
h3, .h3 {
  font-size: 24px;
}
h4, .h4 {
  font-size: 18px;
}
h5, .h5 {
  font-size: 14px;
}
h6, .h6 {
  font-size: 12px;
}
.li-spacing {
  display: inline-block;
  margin: 0 10px;
  height: 12px;
  width: 1px;
  background-color: silver;
}
.vbox {
  display: table;
  position: relative;
  height: 100%;
  width: 100%;
  border-spacing: 0;
}
.vbox > header ~ section {
    top: 60px;
}
.vbox > section {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}
.hbox {
  display: table;
  table-layout: fixed;
  width: 100%;
  border-spacing: 0;
}
.hbox.stretch {
    height: 100%;
}
.hbox > aside,
  .hbox > section {
    vertical-align: top;
    display: table-cell;
    float: none;
    padding: 0;
    height: 100%;
}
.hbox > aside {
    position: relative;
}
html,
body {
  height: 100%;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  min-width: 900px;
  height: 100%;
}



