
.go-out-list {
  position: relative;
  width: 100%;
  height: calc(100% - 40px);
  background: rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
.go-out-list .go-out-wrap {
    width: 100%;
    height: auto;
    margin: 0;
    padding: 15px;
    text-align: left;
    list-style: none;
}
.go-out-list .go-out-wrap .list {
      position: relative;
      width: 100%;
      height: 80px;
      padding-left: 40px;
      font-size: 18px;
}
.go-out-list .go-out-wrap .list:before, .go-out-list .go-out-wrap .list:after {
        content: " ";
        display: table;
}
.go-out-list .go-out-wrap .list:after {
        clear: both;
}
.go-out-list .go-out-wrap .list:after {
        position: absolute;
        left: 12px;
        top: 20px;
        width: 3px;
        height: 80%;
        background: #28ce34;
        content: "";
}
.go-out-list .go-out-wrap .list:last-child:after {
      display: none;
}
.go-out-list .go-out-wrap .bor {
      position: absolute;
      left: 6px;
      top: 6px;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      box-shadow: 0 0 0 6px #28ce34;
}
.go-out-list .go-out-wrap .thing {
      float: right;
}



