
.offcie-warp {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  width: 900px;
  height: 100%;
  padding: 35px 35px 50px 35px;
  border-radius: 6px;
  border: 2px solid rgba(83, 137, 216, 0.9);
  background: rgba(37, 80, 128, 0.9);
}
.offcie-warp .title-warp {
    width: 100%;
    height: 150px;
    color: #ffffff;
    border-bottom: 1px solid #eeeeee;
}
.offcie-warp .title-warp:before, .offcie-warp .title-warp:after {
      content: " ";
      display: table;
}
.offcie-warp .title-warp:after {
      clear: both;
}
.offcie-warp .pic-warp {
    position: relative;
    height: 100%;
    width: 50%;
    padding-left: 150px;
    float: left;
}
.offcie-warp .pic-warp .pic {
      position: absolute;
      left: 0;
      top: 0;
      width: 130px;
      height: 130px;
      background: url("/static/images/ex_pic13.jpg") no-repeat center center;
      background-size: 100% auto;
}
.offcie-warp .pic-warp h1, .offcie-warp .pic-warp h4 {
      margin: 0 0 15px 0;
      line-height: 100%;
      font-weight: normal;
}
.offcie-warp .pic-warp h1 {
      margin-top: 5px;
      margin-bottom: 30px;
}
.offcie-warp .pic-warp h4 {
      font-size: 15px;
}
.offcie-warp .number-warp {
    width: 50%;
    height: 100%;
    float: left;
}
.offcie-warp .number-warp:before, .offcie-warp .number-warp:after {
      content: " ";
      display: table;
}
.offcie-warp .number-warp:after {
      clear: both;
}
.offcie-warp .number-warp .inner-warp {
      width: 25%;
      height: 100%;
      margin-top: 5px;
      text-align: center;
      float: left;
}
.offcie-warp .number-warp .inner-warp p {
        margin: 0;
}
.offcie-warp .number-warp .number {
      width: 75px;
      height: 75px;
      margin-bottom: 15px;
      display: inline-block;
      border-radius: 50%;
      text-align: center;
      line-height: 75px;
      color: #28ce34;
      font-size: 20px;
      background: #ffffff;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.offcie-warp .personnel-list-warp {
    position: relative;
    width: 40%;
    height: calc(100% - 150px);
    float: left;
    overflow: hidden;
}
.offcie-warp .personnel-list-warp h3 {
      color: #ffffff;
}
.offcie-warp .office-list-warp {
    position: relative;
    width: 60%;
    height: calc(100% - 150px);
    float: left;
    overflow: hidden;
}
.offcie-warp .office-list-warp h3 {
      color: #ffffff;
}
.offcie-warp .pf-iner-box {
    width: 100%;
    height: 400px;
}
.go-back {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  margin: auto;
  width: 30px;
  height: 30px;
  color: #ffffff;
  font-size: 15px;
  text-align: center;
  line-height: 26px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  display: block;
}



